@media screen and (min-width: $vp-1200) {
  .footer {
    background-color: #030a1a;
  }

  .footer__container {
    width: 1160px;
    margin: 0 auto;
    margin-top: 41px;
    margin-bottom: 33px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .footer__nav-link {
    font-size: 14px;
    line-height: 17px;
    color: #ffffff;

    &:focus {
      outline: none;
    }
  }

  .footer__logo {
    font-size: 26px;
    line-height: 32px;
    font-weight: 600;
    font-family: $alumni-sans-font;
    font-style: normal;
    color: #ffffff;
  }

  .footer__nav-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .footer__nav-list-item--one-view-action {
    margin-right: 284px;
    margin-top: 2px;
  }

  .footer__nav-list-item:not(:last-child):not(:first-child) {
    margin-right: 40px;
  }

  .footer__social-link:hover {
    background-color: #103aae;
  }

  .footer__social-link:focus-visible {
    background-color: #103aae;
  }

  .footer__social-link:active {
    background-color: $base-color;
  }

  .footer__social-link:focus {
    outline: none;
  }

  .footer__nav-list-item:not(:first-child) {
    position: relative;
  }

  .footer__nav-link--two-view-action {
    &:focus {
      outline: none;
    }

    &:hover::after {
      content: "";
      width: 40px;
      height: 1px;
      background-color: #ffffff;
      position: absolute;
      top: 25px;
      left: 0;
    }

    &:focus-visible::after {
      content: "";
      width: 60px;
      height: 32px;
      border: 1px solid #ffffff;
      position: absolute;
      top: -4px;
      left: -11px;
      background: none;
    }
  }

  .footer__nav-link--three-view-action {
    &:focus {
      outline: none;
    }

    &:hover::after {
      content: "";
      width: 72px;
      height: 1px;
      background-color: #ffffff;
      position: absolute;
      top: 25px;
      left: 0;
    }

    &:focus-visible::after {
      content: "";
      width: 85px;
      height: 32px;
      border: 1px solid #ffffff;
      position: absolute;
      top: -4px;
      left: -8px;
      background: none;
    }
  }

  .footer__nav-link--four-view-action {
    &:focus {
      outline: none;
    }


    &:hover::after {
      content: "";
      width: 35px;
      height: 1px;
      background-color: #ffffff;
      position: absolute;
      top: 25px;
      left: 0;
    }

    &:focus-visible::after {
      content: "";
      width: 47px;
      height: 32px;
      border: 1px solid #ffffff;
      position: absolute;
      top: -4px;
      left: -8px;
      background: none;
    }
  }

  .footer__nav-link--five-view-action {
    &:focus {
      outline: none;
    }

    &:hover::after {
      content: "";
      width: 48px;
      height: 1px;
      background-color: #ffffff;
      position: absolute;
      top: 25px;
      left: 0;
    }

    &:focus-visible::after {
      content: "";
      width: 63px;
      height: 32px;
      border: 1px solid #ffffff;
      position: absolute;
      top: -4px;
      left: -8px;
      background: none;
    }
  }

  .footer__nav-link--six-view-action {
    &:focus {
      outline: none;
    }

    &:hover::after {
      content: "";
      width: 68px;
      height: 1px;
      background-color: #ffffff;
      position: absolute;
      top: 25px;
      left: 0;
    }

    &:focus-visible::after {
      content: "";
      width: 79px;
      height: 32px;
      border: 1px solid #ffffff;
      position: absolute;
      top: -4px;
      left: -8px;
      background: none;
    }
  }

  .footer__social-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 97px;
  }

  .footer__social-link {
    width: 25px;
    height: 25px;
    background-color: #ffffff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}


@media screen and (min-width: $vp-768) and (max-width: 1199px) {
  .footer {
    background-color: #030a1a;
  }

  .footer__container {
    width: 678px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 45px;
    margin-bottom: 40px;
  }

  .footer__nav-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .footer__nav-list-item:not(:last-child):not(:first-child) {
    margin-right: 30px;
  }

  .footer__nav-link {
    font-size: 14px;
    line-height: 17px;
    color: #ffffff;
  }

  .footer__logo {
    font-size: 24px;
    line-height: 29px;
    font-weight: 700;
    font-family: $alumni-sans-font;
    font-style: normal;
    color: #ffffff;
  }

  .footer__nav-list-item--one-view-action {
    margin-right: 69px;
    margin-top: -2px;
  }

  .footer__social-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 97px;
  }

  .footer__social-link {
    width: 25px;
    height: 25px;
    background-color: #ffffff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@media screen and (min-width: 769px) and (max-width: 1199px) {
  .footer__container {
    width: 90%;
  }

  .footer__nav-list {
    width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .footer {
    background-color: #030a1a;
  }

  .footer__container {
    width: 290px;
    margin: 0 auto;
  }

  .footer__nav-link {
    line-height: 19px;
    color: #ffffff;
  }

  .footer__logo {
    font-family: $alumni-sans-font;
    font-size: 20px;
    line-height: 24px;
    font-weight: 600;
    font-style: normal;
    color: #ffffff;
  }

  .footer__social-link {
    width: 25px;
    height: 25px;
    background-color: #ffffff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .footer__social-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 97px;
  }
}

@media screen and (max-width: 767px) {
  .footer__nav-list {
    display: grid;
    grid-template-columns: 89px 89px;
    grid-template-rows: 16px 16px 16px;
    column-gap: 71px;
    row-gap: 25px;
  }

  .footer__container {
    margin-top: 45px;
    margin-bottom: 42px;
  }

  .footer__social-list {
    margin-top: 40px;
  }

  .footer__nav-list-item--two-position-action {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
  }

  .footer__nav-list-item--three-position-action {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 3;
    grid-row-end: 4;
  }

  .footer__nav-list-item--four-position-action {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
  }

  .footer__nav-list-item--five-position-action {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
  }
}

@media screen and (min-width: 321px) and (max-width: 767px) {
  .footer__container {
    width: 90%;
  }

  .footer__nav-list {
    width: 100%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
}
